<template>
  <section class="clockContent">
    <div class="clock">
      <div class="c_top" :style="'background:'+ color">等待上钟</div>
      <div class="c_content">
        <div class="c_time">
          <p>{{title}}</p>
          <p>{{propJson.time}}</p>
        </div>
        <div class="c_line">
          <p>客户手牌：{{propJson.cardNo}}</p>
          <p :style="'color:'+ color">点钟</p>
        </div>
        <div class="c_line">
          <p>位置点：{{propJson.serviceNo}}</p>
          <p v-show="propJson.num">技师工号：{{propJson.num}}</p>
        </div>
        <div class="c_line">
          <p>服务项目：{{propJson.serviceName}}</p>
        </div>
        <div class="c_line widthLine" v-show="propJson.upTime">
          <p>
            预计下钟时间：
            <span>{{propJson.upTime}}</span>
          </p>
        </div>
        <div class="c_line" v-show="propJson.starTime">
          <p>开始上钟时间：{{propJson.starTime}}</p>
        </div>
      </div>
      <div class="c_bottom">
        <div class="c_btn" :style="'background:'+ color" @click='onClick(btnTitle)'>{{btnTitle}}</div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  methods:{
    onClick(e){
      this.$emit('onclock',e)
    }
  },
  props: ['propJson', 'title', 'color','btnTitle']
};
</script>

<style lang="scss" scoped>
.clockContent {
  background-color: $fc;
  .clock {
    @include fc(center);
    @include sc(0.8rem, $fColor);
    .c_top {
      padding: 0.5rem;
      // background-color: $blue;
      @include sc(0.8rem, $fc);
      @include wh(100%, 100%);
      text-align: left;
    }
    .c_content {
      @include wh(100%, 100%);
      padding: 0rem 0.5rem;
      .c_time {
        margin-top: 1rem;
        border-bottom: 0.02rem solid $F5;
        p {
          font-weight: bold;
        }
        p:last-child {
          @include sc(0.95rem, $fColor);
          padding: 0.5rem 0rem 1rem 0rem;
        }
      }
      .c_line {
        @include fr(space-between);
        border-bottom: 0.02rem solid $F5;
        padding: 0.8rem 0.5rem;
      }
      .widthLine {
        padding: 1.3rem 0.5rem;
        span {
          @include sc(1.1rem, $fColor);
          font-weight: bold;
        }
      }
    }
    .c_bottom {
      @include wh(100%, 100%);
      @include fr(center);
      padding: 1.2rem 0rem;
      .c_btn {
        @include wh(70%, 2rem);
        @include sc(0.9rem, $fc);
        @include fr(center);
        border-radius: 0.3rem;
      }
    }
  }
}
</style>