<template>
  <div class="clock_Content">
    <clock-content
      :propJson="colocJson1"
      :title="title"
      :color="color"
      :btnTitle="btnTitle"
      @onclock="clickBtn"
    />
    <div class="btn">
      <van-button
        type="primary"
        size="large"
        color="linear-gradient(to right, #4bb0ff, #6149f6)"
        to="/clockRecord"
      >历史记录</van-button>
    </div>
    <van-dialog
      v-model="show"
      title="上钟提示"
      :showCancelButton="showCancelButton"
      confirm-button-text="我知道了"
    >
      <div class="dialogContent">
        <img src="https://img.yzcdn.cn/vant/apple-3.jpg" />
        <p>客户手牌9999的中式足浴经典套餐服务时间已开始，请点击【上钟】</p>
      </div>
    </van-dialog>
  </div>
</template>

<script>
import clockContent from "@/components/clockContent";

export default {
  data() {
    return {
      show: false,
      showCancelButton: false,
      title: "下单时间",
      color: "#59B0FC",
      btnTitle: "我要上钟",
      colocJson1: {
        time: "2019/09/25  16:12",
        cardNo: "999",
        c_state: "1",
        serviceNo: "001",
        serviceName: "全身按摩（60分钟）"
      }
    };
  },
  methods: {
    clickBtn(e) {
      this.common_vant.openLoading();
      var _self = this;
      if (e === "我要上钟") {
        setTimeout(function() {
          _self.common_vant.closeLoading();
          _self.title = "上钟时间";
          _self.color = "#908CF9";
          _self.btnTitle = "我要下钟";
          _self.colocJson1 = {
            time: "2019/09/25  16:12",
            cardNo: "999",
            c_state: "1",
            num: "A002",
            serviceNo: "001",
            upTime: "2019/12/18 00:00:00",
            serviceName: "全身按摩（60分钟）"
          };
        }, 800);
      } else if (e === "我要下钟") {
        if (
          _self.util.getTime(_self.colocJson1.upTime) <
          _self.util.getTime(new Date())
        ) {
          setTimeout(function() {
            _self.common_vant.closeLoading();
            _self.title = "下钟时间";
            _self.color = "#FF6060";
            _self.btnTitle = "服务结束";
            _self.colocJson1 = {
              time: "2019/09/25  16:12",
              cardNo: "999",
              c_state: "1",
              num: "A002",
              serviceNo: "001",
              starTime: _self.util.getTime(new Date()),
              serviceName: "全身按摩（60分钟）"
            };
          }, 800);
        } else {
          _self.common_vant.openToast("未到达预计下钟时间");
        }
      } else {
        setTimeout(function() {
          _self.common_vant.closeLoading();
          _self.$router.push("/");
        }, 800);
      }
    }
  },
  components: {
    clockContent
  }
};
</script>

<style lang="scss" scoped>
.clock_Content {
  padding: 0.5rem;
  .dialogContent {
    margin: 0.8rem;
    img {
      @include wh(100%, 40%);
    }
    p {
      text-align: left;
      @include sc(0.8rem, $line);
    }
  }
}
.btn{
  position: fixed;
  bottom: 1rem;
  @include wh(95%,auto);
}
</style>